let sex=['男','女'];
let qg=['秘密','未婚','已婚'];
let job=['秘密','IT','老师',"学生"];
import {mapState, mapMutations} from 'vuex'
import mpvueCityPicker from "../../components/mpvue-citypicker/mpvueCityPicker.vue";
export default {
	components:{
		mpvueCityPicker
	},

	data() {
		return {
			userpic:"",
			username:"",
			sex:"",
			job:"",
			birthday:"",
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '',
			authorFile: undefined
		}
	},
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
	  	this.$refs.mpvueCityPicker.pickerCancel();
	    return true;
	  }
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	mounted() {
		if(this.userInfo&&this.userInfo.id){
			this.userpic = this.userInfo.authorUrl
			this.username = this.userInfo.userName
			this.sex = this.userInfo.gender==0?"男":"女"
			this.job = this.userInfo.occupation
			this.birthday = this.userInfo.birthday
			this.cityPickerValueDefault =  [0, 0, 1]
			this.pickerText =  this.userInfo.address
		}
	},
	computed: {
		...mapState(['userInfo']),
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		...mapMutations(['setUserInfo']),
		// 三级联动
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.pickerText = e.label;
		},
		// 修改生日
		bindDateChange(e) {
			this.birthday = e.target.value
		},
		// 修改头像
		changeimg(){
			uni.chooseImage({
				count:1,
				sizeType:['compressed'],
				success: (res) => {
					this.userpic = res.tempFiles[0].path
					this.authorFile = res.tempFiles[0]
				}
			})
		},
		// 单列选择
		changeOne(val){
			let arr=[];
			switch (val){
				case 'sex':
				arr=sex;
					break;
				case 'qg':
				arr=qg;
					break;
				case 'job':
				arr=job;
					break;
			}
			uni.showActionSheet({
				itemList: arr,
				success: res => {
					switch (val){
						case 'sex':
						this.sex=arr[res.tapIndex];
							break;
						case 'qg':
						this.qg=arr[res.tapIndex];
							break;
						case 'job':
						this.job=arr[res.tapIndex];
							break;
					}
				},
			});
		},
		async submit(){
			let url = this.userpic;
			if(this.authorFile!=undefined){
				 url = await this.$http.uploudFile('upload/cloud', this.authorFile)
			}
			
			let data = {
				authorUrl: url,
				userName:this.username,
				gender:this.sex=="男"?0:1,
				occupation:this.job,
				birthday:this.birthday,
				address: this.pickerText,
			}
			let res = await this.$http.put("user/info",data)
			if(res.code==0){
				let data = await this.$http.get("user/info")
				if(data.code==0){
					this.setUserInfo(data.data.userInfo)
					uni.setStorageSync('token',data.data.token)
					uni.setStorageSync('userInfo',JSON.stringify(data.data.userInfo))
				}
				console.log(data)
			}
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
